  background: #f4645f;
  border-radius: 2px;
  color: #fff;
  position: fixed;
  right: 15px;
  bottom: 20px;
  opacity: 0;
  z-index: 9999;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  transition: opacity 0.3s cubic-bezier(0.55, 0, 0.1, 1) 0.3s, bottom 0.3s cubic-bezier(0.55, 0, 0.1, 1) 0.3s; }
  .preloader.visible {
    opacity: 1;
    bottom: 30px;
    transition: none; }
  .preloader:after {
    content: '';
    display: block;
    position: absolute;
    right: 15px;
    border: transparent 8px solid;
    border-top-color: #f4645f;
    bottom: -16px; }
  .preloader .circle {
